.body {
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  width: 100%;
  min-height: 100%; }

.container {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  max-width: 1024px; }

.header {
  background: url("../img/headerBike.jpg") no-repeat center top, linear-gradient(#0D0D0D, #101010, #202020); }
  @media (min-resolution: 2dppx) {
    .header {
      background: url("../img/headerBike@2x.jpg") no-repeat center top, linear-gradient(#0D0D0D, #101010, #202020); } }
  @media (max-width: 1024px) {
    .header {
      background: none;
      background-color: #FFFFFF; } }

.header__container {
  max-width: 880px; }
  @media (max-width: 1024px) {
    .header__container {
      max-width: 1024px; } }

.header__toggle {
  display: none;
  padding: 0;
  position: absolute;
  width: 50px;
  height: 50px;
  border: 0;
  right: 0;
  top: 0;
  z-index: 5; }
  @media (max-width: 1024px) {
    .header__toggle {
      display: block; } }
  .header__toggle--open {
    background: #0AD9C6 url("../../img/menu.svg") no-repeat center; }
  .header__toggle--close {
    background: #FFFFFF url("../img/cross.svg") no-repeat center; }

.header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  gap: 155px;
  padding-bottom: 218px; }
  @media (max-width: 1024px) {
    .header__wrapper {
      padding-bottom: 0;
      gap: 0;
      flex-direction: column;
      text-align: center; } }

.header__title-wrapper p {
  font-size: 20px;
  line-height: 32px;
  font-weight: 300;
  color: #FFFFFF;
  letter-spacing: -0.1px;
  opacity: 0.8; }
  @media (max-width: 1024px) {
    .header__title-wrapper p {
      font-size: 18px;
      line-height: 32px;
      font-weight: 300;
      color: #FFFFFF; } }

.header__title-wrapper br {
  display: none; }

@media (max-width: 1024px) {
  .header__title-wrapper {
    background: url("../img/headerBike.jpg") no-repeat center top;
    padding-top: 107px;
    padding-bottom: 61px;
    background-position: top 50% center;
    background-size: cover; } }
  @media (max-width: 1024px) and (min-resolution: 2dppx) {
    .header__title-wrapper {
      background: url("../img/headerBike@2x.jpg") no-repeat center top; } }

@media (max-width: 1024px) {
    .header__title-wrapper br {
      display: block; } }

@media (max-width: 767px) {
  .header__title-wrapper {
    background: url("../img/headerBike-mobile.jpg") no-repeat center top;
    padding-top: 93px;
    padding-bottom: 51px;
    background-size: cover;
    background-position: center; } }
  @media (max-width: 767px) and (min-resolution: 2dppx) {
    .header__title-wrapper {
      background: url("../../img/headerBike-mobile@2x.jpg") no-repeat center top; } }

@media (max-width: 767px) {
    .header__title-wrapper p {
      padding: 0 15px; }
    .header__title-wrapper br {
      display: block; } }

@media (max-width: 1024px) {
  .header__logo {
    position: absolute;
    top: 25px;
    left: 21px; } }

@media (max-width: 767px) {
  .header__logo {
    left: 16px; } }

.header__title {
  margin: 28px 0 24px 0;
  font-size: 46px;
  line-height: 64px;
  font-weight: 700;
  color: #FFFFFF; }
  @media (max-width: 1024px) {
    .header__title {
      margin: 28px 0 20px 0;
      font-size: 30px;
      line-height: 42px;
      font-weight: 700;
      color: #FFFFFF; } }

.header__form-wrapper p {
  margin: 4px 0 20px 0;
  text-align: center;
  font-size: 17px;
  line-height: 28px;
  font-weight: 600;
  color: #FFFFFF; }

.header__form-wrapper form {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

.header__form-wrapper button {
  background-color: #0AD9C6;
  height: 51px;
  border: 0;
  border-radius: 6px;
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  color: #000000; }

.header__form-wrapper input {
  margin-bottom: 20px;
  height: 48px;
  border: 1px solid #C9CCD4;
  border-radius: 6px;
  padding: 14px 0 10px 29px; }
  .header__form-wrapper input::placeholder {
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
    line-height: 24px;
    font-weight: 400;
    color: #1C1C1C;
    opacity: 0.5; }
  .header__form-wrapper input::-webkit-outer-spin-button, .header__form-wrapper input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

@media (max-width: 1024px) {
  .header__form-wrapper {
    width: 42.2%;
    margin: 67px auto 0 auto; }
    .header__form-wrapper p {
      color: #000000;
      font-size: 18px;
      margin-bottom: 30px; }
    .header__form-wrapper input {
      padding: 12px 0 12px 20px; } }

@media (max-width: 767px) {
  .header__form-wrapper {
    width: auto;
    padding: 0 16px; }
    .header__form-wrapper p {
      margin-bottom: 20px; }
    .header__form-wrapper input {
      padding: 13px 0 11px 25px; }
      .header__form-wrapper input:last-of-type {
        margin-bottom: 19px; } }

.nav {
  margin-bottom: 256px; }
  @media (max-width: 1024px) {
    .nav {
      margin-bottom: 0; } }

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  padding: 21px 24px 10px 21px;
  align-items: flex-end; }
  .nav__list::after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.2);
    left: 0;
    bottom: 0; }
    @media (max-width: 1024px) {
      .nav__list::after {
        display: none; } }

@media (max-width: 1024px) {
  .nav__list--close {
    display: none; } }

@media (max-width: 1024px) {
  .nav__list--open {
    display: block;
    position: absolute;
    z-index: 2; } }

.nav__item:last-child {
  margin-bottom: 0; }

@media (max-width: 1024px) {
  .nav__item {
    text-align: left; } }

.nav__link {
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  color: white;
  opacity: 70%; }

@media (max-width: 1024px) {
  .nav__list {
    width: 100%;
    flex-direction: column;
    align-items: flex-start;
    padding: 100px 0 138px 0;
    background-color: #FFFFFF; }
    .nav__list .nav__item {
      background-color: #F6F6F6;
      width: 100%;
      margin-bottom: 30px; }
    .nav__list .nav__link {
      position: relative;
      font-size: 20px;
      line-height: 28px;
      font-weight: 400;
      color: #48494D;
      padding: 17px 35px 17px 16px;
      width: 100%;
      display: block;
      opacity: 1; }
      .nav__list .nav__link::after {
        content: "";
        display: block;
        position: absolute;
        width: 25px;
        height: 25px;
        right: 16px;
        top: 17px;
        background: url("../img/arrow.svg") no-repeat center; } }
